:root {
  --color-white: #fff;
  --color-light-white: #ffffffb2;
  --color-black: #111;
  --color-dark-purple: #1e1823;
  --color-orange: #fd9222;
  --color-light-orange: #fcf1e0;
  --white-color: #fff;
  --snow-color: #fafafa;
  --linen-color: #fcf1e0;
  --carrot-orange-color: #fd9222;
  --eerie-black-color: #1e1823;
  --smoky-black-color: #111;
  --smoky-transparent-black-color: #1111111a;
  --font-montserrat: "Montserrat", sans-serif;
  --description-font-size: 14px;
  --title-font-size: 24px;
  --mobile-title-font-size: 20px;
  --sub-title-font-size: 18px;
  --pop-up-font-size: 10px;
  --logo-weight: 700;
  --bold-weight: 600;
  --medium-weight: 500;
  --light-weight: 400;
  --mobile-breakpoint: 375px;
  --tablet-breakpoint: 768px;
  --desktop-breakpoint: 1200px;
  --animation-duration: .3s;
}

* {
  box-sizing: border-box;
  font-family: var(--font-montserrat);
  outline: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body, h1, h2, h3, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

nav ul {
  list-style: none;
}

html {
  background: #fafafa;
}

.container {
  width: 375px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    margin: 0 auto;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

header {
  color: #0ff;
  background-color: brown;
}

.hero a, .hero button {
  color: var(--white-color);
  text-decoration: none;
}

.hero {
  height: 690px;
}

.hero-container {
  width: 335px;
  height: 621px;
  background-color: #d3d3d3;
  background-image: linear-gradient(#1110 0%, #1116 100%), url("hero-bg-mobile@1x.bcd00ade.jpg"), none;
  background-position: 0 0, 0 0, -438.076px -7.879px;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll, scroll;
  background-origin: padding-box, padding-box, padding-box;
  background-clip: border-box, border-box, border-box;
  border-radius: 30px;
  flex-direction: column;
  gap: 32px;
  margin: 0 auto;
  padding: 395px 20px 20px;
  display: flex;
}

.hero-sub-title {
  color: var(--white-color);
  letter-spacing: -.6px;
  font-family: Montserrat, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.hero-btns-container {
  flex-direction: column;
  gap: 32px;
  display: flex;
}

.hero-main-btns {
  gap: 14px;
  display: flex;
}

.hero-link {
  width: 159px;
  height: 41px;
  border: 1px solid var(--white-color);
  font-family: var(--font-montserrat);
  transition: var(--animation-duration) ease-in-out;
  border-radius: 100px;
  padding: 12px 28px;
  font-size: 13px;
  font-weight: 600;
}

.hero-link:hover, .hero-link:focus {
  color: var(--carrot-orange-color);
  border: 1px solid var(--carrot-orange-color);
}

.hero-buy-btn {
  background: var(--carrot-orange-color);
  cursor: pointer;
  border-color: #0000;
  border-radius: 100px;
  padding: 10px 28px;
  font-family: Montserrat, sans-serif;
  font-size: 13px;
  font-weight: 600;
  transition: all .3s ease-in-out;
}

.hero-buy-btn:hover, .hero-buy-btn:focus {
  color: var(--carrot-orange-color);
  background-color: #fcf1e0;
}

.hero-scroll-container {
  align-items: center;
  gap: 12px;
  display: flex;
}

.hero-scroll {
  font-family: var(--font-montserrat);
  letter-spacing: -.36px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  transition: all .3s ease-in-out;
}

.hero-scroll-icon {
  width: 28px;
  height: 28px;
  background-color: var(--white-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
  overflow: hidden;
}

.hero-scroll-icon .hero-scroll-svg {
  animation: linear infinite arrow;
}

.hero-scroll-icon:hover .hero-scroll-svg, .hero-scroll:hover + .hero-scroll-icon .hero-scroll-svg {
  animation-duration: .7s;
}

@keyframes arrow {
  0% {
    transform: translateY(-25px);
  }

  1% {
    transform: translateY(-25px);
  }

  50% {
    transform: translateY(0);
  }

  51% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(26px);
  }
}

.hero-scroll-svg {
  fill: var(--carrot-orange-color);
  transition: var(--animation-duration) ease-in-out;
}

.hero-scroll:hover, .hero-scroll:focus {
  color: var(--carrot-orange-color);
}

.hero-scroll:hover + .hero-scroll-icon, .hero-scroll:focus + .hero-scroll-icon {
  background-color: var(--carrot-orange-color);
}

.hero-scroll:hover + .hero-scroll-icon .hero-scroll-svg, .hero-scroll:focus + .hero-scroll-icon .hero-scroll-svg, .hero-scroll-svg:hover, .hero-scroll-svg:focus, .hero-scroll-icon:hover .hero-scroll-svg, .hero-scroll-icon:focus .hero-scroll-svg {
  fill: var(--linen-color);
}

.hero-scroll-icon:hover, .hero-scroll-icon:focus {
  background-color: var(--carrot-orange-color);
}

